import {mapMutations} from 'vuex';
import {CHANGECARTLISTMUTATION} from '../../store/mutation-type.js';
import request from '../../utils/request.js';
export default {
	data() {
		return {
			goodDetail:{}
		}
	},
	async mounted(){
		// console.log(this.$mp.query.goodId)
		let {goodId} = this.$mp.query;
		let goodData=await request('/getGoodDetail',{id:goodId});
		// console.log(goodData)
		this.goodDetail=goodData.data;
		
	},
	methods:{
		...mapMutations({
			changeCartList:CHANGECARTLISTMUTATION
		}),
		addShopCart(){
			console.log('添加到购物车')
			this.changeCartList(this.goodDetail);
		}
	}
}
